import countryService from '../services/countriesData'

const countryDataReducer = (state = {}, action) => {
  switch (action.type) {
    case 'COUNTRY_CIA_DATA':
      if (action.data.toString().includes('Error')) {
        return { data: 'no data' }
      } else {
        return action.data
      }
    default:
      return state
  }
}

export const allCountriesDataReducer = (state = [], action) => {
  switch (action.type) {
    case 'ALL_COUNTRIES_CIA_DATA':
   //   console.log('cia reducer state: ',state)
  //    console.log('cia reducer action: ',action.data)
      return action.data
    default:
      return state
  }
}

export const getCountryData = (country) => {
  return async (dispatch) => {
    const countryData = await countryService.getCiaData(country)
    dispatch({
      type: 'COUNTRY_CIA_DATA',
      data: countryData,
    })
  }
}

export const getAllCountriesData = () => {
  return async (dispatch) => {
    const countriesData = await countryService.getAllCiaData()
    dispatch({
      type: 'ALL_COUNTRIES_CIA_DATA',
      data: countriesData,
    })
  }
}

export default countryDataReducer
