import React, { useState, useEffect } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import {
  Image,
  Card,
  Accordion,
  Label,
  Icon,
  Segment,
  Header,
} from 'semantic-ui-react'
import { getWeather } from '../reducers/weatherReducer'
import { getTimeZone } from '../reducers/timeZoneReducer'
import { getCountryData } from '../reducers/countryDataReducer'
import zoomLevel from '../zoomLevels'
import loadable from '@loadable/component'

const Map = loadable(() => import('../pages/Map'))
const Weather = loadable(() => import('../components/WeatherMobile'))

const Country1 = ({ data }) => {
  const [activeIndex, setActiveIndex] = useState(3)

  const dispatch = useDispatch()

  const weather = useSelector((state) => state.weather)
  const unit = useSelector((state) => state.unit.unit)

  const location =
    data.capitalInfo.latlng === undefined
      ? data.latlng
      : data.capitalInfo.latlng

  useEffect(() => {
    dispatch(getCountryData(data.name.common))
  }, [data])

  useEffect(() => {
    dispatch(
      getWeather({
        location: location,
        unit: unit,
      })
    )
    dispatch(getTimeZone({ location: location }))
  }, [unit])

  const handleClick = (e, titleProps) => {
    const { index } = titleProps
    activeIndex === index ? setActiveIndex(-1) : setActiveIndex(index)
  }

  const weatherData = Object.entries(weather).length

  return weatherData < 1 ? (
    <>Loading...</>
  ) : (
    <>
      <Segment inverted textAlign="center">
        <Header>{data.name.common}</Header>
      </Segment>
      {/* ------------------ Map -------------------- */}
      <Accordion fluid>
        <Accordion.Title
          active={activeIndex === 0}
          index={0}
          onClick={handleClick}
        >
          <Icon name="dropdown" />
          Map
        </Accordion.Title>
        <Accordion.Content active={activeIndex === 0}>
          <Map lt={data.latlng[0]} lg={data.latlng[1]} zm={zoomLevel(data)} />
        </Accordion.Content>
        {/* ------------------ Flag -------------------- */}
        <Accordion.Title
          active={activeIndex === 1}
          index={1}
          onClick={handleClick}
        >
          <Icon name="dropdown" />
          Flag
        </Accordion.Title>
        <Accordion.Content active={activeIndex === 1}>
          <Image
            id="flag"
            centered
            src={data.flags.svg}
            alt="country flag"
            size="medium"
            bordered
          />
        </Accordion.Content>
        {/* ------------------ Coat of arms -------------------- */}
        <Accordion.Title
          active={activeIndex === 2}
          index={2}
          onClick={handleClick}
        >
          <Icon name="dropdown" />
          Coat Of Arms
        </Accordion.Title>
        <Accordion.Content active={activeIndex === 2}>
          <>
            {Object.entries(data.coatOfArms).length === 0 ? (
              <Label>No image available</Label>
            ) : (
              <Image
                id="coatOfArms"
                centered
                src={data.coatOfArms.svg}
                alt="coat of arms"
                size="medium"
                bordered
              />
            )}
          </>
        </Accordion.Content>
        {/* ------------------ Weather -------------------- */}
        <Accordion.Title
          active={activeIndex === 3}
          index={3}
          onClick={handleClick}
        >
          <Icon name="dropdown" />
          Weather
        </Accordion.Title>
        <Accordion.Content active={activeIndex === 3}>
          <Card id="CiaCard" fluid>
            <Weather country={data} />
          </Card>
        </Accordion.Content>
      </Accordion>
    </>
  )
}

const Country = React.memo(Country1)

export default Country
